
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import util from "../util";
import { saveImg } from "../cordovaplugin";

export default {
data() {
return {

pageShow: false,

messageBadge: "80",

popup: {
popupState: false,
type: "reward"
},

rewards: {
list: [0.01, 0.1, 0.5, 1, 5, 10],
seleted: -1
},

cellArr: [





{
title: "支付宝",
lefticonclass: "icon-zhifubao",
righticonclass: "icon-weixuanzhong"
}
],
selectPayMethod: "",

confirmReward: "",

menuBoxShow: false,

showReportPopup: false,

reportCellArr: [
{
title: "淫秽色情",
lefticonclass: "",
righticonclass: "icon-weixuanzhong"
},
{
title: "违法信息",
lefticonclass: "",
righticonclass: "icon-weixuanzhong"
},
{
title: "营销广告",
lefticonclass: "",
righticonclass: "icon-weixuanzhong"
},
{
title: "恶意攻击谩骂",
lefticonclass: "",
righticonclass: "icon-weixuanzhong"
}
],
previewList: {
list: [],
index: 0,
userId: ""
},
tab: {
tabList: [{ id: "0", tits: "全部" }],
id: 0
},

baseList: [],

designList: [],

currentDesignItem: null,

currentPreviewDesignItem: null,

userInfo: null,
pageNum: 1,
sharePopup: false
};
},
created: function() {},
mounted: async function() {
let userInfo = localStorage.getItem("userInfo");
if (userInfo) {
this.userInfo = JSON.parse(userInfo);
}
this.$vux.loading.show({
text: "Loading"
});
/*
** 话题列表
*/
let tabList = await util.getData({
url: "/materials/topicslists",
method: "post"
});
this.tab.tabList = this.tab.tabList.concat(tabList);
/*
** 轮播图
*/
this.bannerList = await util.getData({
url: "/materials/bannerslists",
method: "post"
});
this.bannerList.forEach((item, index) => {
item = {
img: item.thumbs
};
this.baseList.push(item);
});
/*
** 随手拍列表
*/
let designListUrl;
if (this.userInfo) {
designListUrl = `/materials/postslists?loginid=${
this.userInfo.id
}&topics_id=${this.tab.id}&pages=${this.pageNum}&rowsmax=10`;
} else {
designListUrl = `/materials/postslists?topics_id=${this.tab.id}&pages=${
this.pageNum
}&rowsmax=10`;
}
this.designList = await util.getData({
url: designListUrl,
method: "get"
});
this.pageNum++;

this.designList.forEach((item, index) => {

item.previewList = [];
item.images.forEach(img => {
item.previewList.push({
id: img.id,
img: img.updatas,
collected: img.collected || 0
});
});

item.sex = item.gender == 1 ? "boy" : "girl";

switch (item.previewList.length) {
case 1:
item.class = "one-img";
break;
case 2:
item.class = "two-img";
break;
case 3:
item.class = "three-img";
break;
case 4:
item.class = "four-img";
break;
case 5:
item.class = "five-img";
break;
case 6:
item.class = "fix-img";
break;
case 7:
item.class = "seven-img";
break;
case 8:
item.class = "eight-img";
break;
case 9:
item.class = "nine-img";
break;

default:
break;
}
});
this.$vux.loading.hide();
this.pageShow = true;
},
methods: {

async seleted(id) {
this.tab.id = id;
this.pageNum = 1;
this.$vux.loading.show({
text: "Loading"
});
/*
** 随手拍列表
*/
let designListUrl;
if (this.userInfo) {
designListUrl = `/materials/postslists?loginid=${
this.userInfo.id
}&topics_id=${this.tab.id}&pages=${this.pageNum}&rowsmax=10`;
} else {
designListUrl = `/materials/postslists?topics_id=${this.tab.id}&pages=${
this.pageNum
}&rowsmax=10`;
}
this.designList = await util.getData({
url: designListUrl,
method: "get"
});
this.pageNum++;

this.designList.forEach((item, index) => {

item.previewList = [];
item.images.forEach(img => {
item.previewList.push({
id: img.id,
img: img.updatas,
collected: img.collected || 0
});
});

item.sex = item.gender == 1 ? "boy" : "girl";

switch (item.previewList.length) {
case 1:
item.class = "one-img";
break;
case 2:
item.class = "two-img";
break;
case 3:
item.class = "three-img";
break;
case 4:
item.class = "four-img";
break;
case 5:
item.class = "five-img";
break;
case 6:
item.class = "fix-img";
break;
case 7:
item.class = "seven-img";
break;
case 8:
item.class = "eight-img";
break;
case 9:
item.class = "nine-img";
break;

default:
break;
}
});
this.$vux.loading.hide();
},
async imgCollect(imgs, userId) {
if (this.userInfo) {
if (imgs.collected == 1) {
this.$vux.alert.show({
title: "提示",
content: "不能重复收藏"
});
return false;
}
let data = await util.request({
url: `/materials/collects`,
method: "post",
param: {
loginid: this.userInfo.id,
users_mains_id: userId,
targets_id: imgs.id,
types: 1
}
});
if (data.return_code == "success") {
this.$vux.toast.show({
text: "收藏成功"
});
imgs.collected = 1;
} else {
this.$vux.alert.show({
title: "提示",
content: data.return_data
});
}
} else {
this.$vux.confirm.show({
title: "提示",
content: "请先登录",
onConfirm: () => {
this.$router.push({
path: "/page/user/LoginIndex"
});
}
});
}
},

showMenu(designItem) {
this.menuBoxShow = !this.menuBoxShow;
this.currentDesignItem = designItem;
},

toogleReportPopup() {
this.menuBoxShow = false;
this.showReportPopup = !this.showReportPopup;
},

selectReport(report) {
this.confirmContent = report.title;
this.reportCellArr.forEach((item, index) => {
item.righticonclass = "icon-weixuanzhong";
});
report.righticonclass = "icon-xuanzhong";
},

async reportCommit() {
if (this.confirmContent) {
this.$vux.loading.show({
text: "Loading"
});
let data = await util.request({
url: `/materials/reports`,
method: "post",
param: {
loginid: this.userInfo.id,
users_mains_id: this.currentDesignItem.userid,
reasons: this.confirmContent,
types: 2,
targets_id: this.currentDesignItem.sources_id
}
});
if (data.return_code == "success") {
this.$vux.toast.show({
text: data.return_data
});
this.showReportPopup = false;
this.confirmContent = "";
} else {
this.$vux.alert.show({
title: "提示",
content: data.return_data
});
}
this.$vux.loading.hide();
} else {
this.$vux.alert.show({
title: "提示",
content: "请选择举报内容"
});
}
},

show(index, designItem) {
this.previewList.index = index;
this.previewList.list = designItem.previewList;
this.previewList.userId = designItem.userid;
this.currentPreviewDesignItem = designItem;
},

previewClose() {
this.previewList.list = [];
},

toDetail(designItem, type) {
this.$router.push({
path: "/page/take_photo/DesignDetail",
query: { id: designItem.sources_id, type: type }
});
},

togglePopup(type) {
this.popup.type = type;
this.popup.popupState = !this.popup.popupState;
},

rewardSelected(index) {
this.confirmReward = this.rewards.list[index];
this.rewards.seleted = index;
this.toPay();
},

toPay() {
this.popup.type = "pay";
},
backSelectReward() {
this.confirmReward = "";
this.popup.type = "reward";
},
selectPay(cell) {
this.selectPayMethod = cell.title;
this.cellArr.forEach((item, index) => {
item.righticonclass = "icon-weixuanzhong";
});
cell.righticonclass = "icon-xuanzhong";
},

async payCommit() {
if (this.userInfo) {
if (!this.selectPayMethod) {
this.$vux.alert.show({
title: "提示",
content: "请选择付款方式"
});
} else if (this.selectPayMethod == "支付宝") {
let data = await util.getData({
url: `/materials/praisepays`,
method: "post",
param: {
loginid: this.userInfo.id,
users_mains_id: this.currentPreviewDesignItem.userid,
moneys: this.confirmReward
}
});
const orderstring = data; 
window.Alipay.pay(
orderstring,
res => {
this.$vux.toast.show({
text: "打赏成功"
});
},
e => {
console.error(e);
}
);
}
} else {
this.$vux.confirm.show({
title: "提示",
content: "请先登录",
onConfirm: () => {
this.$router.push({
path: "/page/user/LoginIndex"
});
}
});
}
},
async follow(users) {
let types = users.followed ? 0 : 1;
if (this.userInfo) {
let data = await util.request({
url: `/materials/usersfollows`,
method: "post",
param: {
users_mains_id: users.userid,
loginid: this.userInfo.id,
types: types
}
});
if (data.return_code == "success") {
users.followed = types;
this.designList.forEach((item, index) => {
if (users.userid == item.userid) {
item.followed = users.followed;
}
});
}
this.$vux.toast.show({
text: data.return_data
});
} else {
this.$vux.confirm.show({
title: "提示",
content: "请先登录",
onConfirm: () => {
this.$router.push({
path: "/page/user/LoginIndex"
});
}
});
}
},

async liking(users) {
if (this.userInfo) {
if (users.praised == 1) {
this.$vux.alert.show({
title: "提示",
content: "不能重复点赞"
});
return false;
}
let data = await util.request({
url: `/materials/scores`,
method: "post",
param: {
users_mains_id: users.userid,
loginid: this.userInfo.id,
types: 1,
scores: 5,
targets_id: users.sources_id
}
});
if (data.return_code == "success") {
users.praised = 1;
users.praisesnums++;
this.$vux.toast.show({
text: data.return_data
});
} else {
this.$vux.alert.show({
title: "提示",
content: data.return_data
});
}
} else {
this.$vux.confirm.show({
title: "提示",
content: "请先登录",
onConfirm: () => {
this.$router.push({
path: "/page/user/LoginIndex"
});
}
});
}
},
onInfinite(done) {
if (this.pageNum == 1) {
done();
return false;
}
setTimeout(async () => {
let designListUrl;
if (this.userInfo) {
designListUrl = `/materials/postslists?topics_id=${
this.tab.id
}&pages=${this.pageNum}&rowsmax=10`;
} else {
designListUrl = `/materials/postslists?topics_id=${
this.tab.id
}&pages=${this.pageNum}&rowsmax=10`;
}
let designList = await util.getData({
url: designListUrl,
method: "get"
});
if (designList == "") {
this.$refs.scroller.finishInfinite(2);
return false;
}
this.designList = this.designList.concat(designList);
this.pageNum++;

this.designList.forEach((item, index) => {

item.previewList = [];
item.images.forEach(img => {
item.previewList.push({
id: img.id,
img: img.updatas,
collected: img.collected || 0
});
});

item.sex = item.gender == 1 ? "boy" : "girl";

switch (item.previewList.length) {
case 1:
item.class = "one-img";
break;
case 2:
item.class = "two-img";
break;
case 3:
item.class = "three-img";
break;
case 4:
item.class = "four-img";
break;
case 5:
item.class = "five-img";
break;
case 6:
item.class = "fix-img";
break;
case 7:
item.class = "seven-img";
break;
case 8:
item.class = "eight-img";
break;
case 9:
item.class = "nine-img";
break;

default:
break;
}
});
done();
}, 1000);
},

async downs(imgs) {
let data = await util.convertImg(imgs.img, 675, 934);
saveImg(data)
.then(path => {
if (path) {
this.$vux.toast.show({
text: "保存成功"
});
}
})
.catch(error => {
this.$vux.alert.show({
title: "提示",
content: error
});
});
},
selectShare(imgs) {
this.shareImg = imgs.img;
this.sharePopup = true;
},

share(type) {
window.Wechat.share({
message: {
title: '随手拍',
description: '随手拍',
thumb: this.shareImg,
media: {
type: window.Wechat.Type.IMAGE,
image: this.shareImg
}
},
scene: type == 'friends' ? window.Wechat.Scene.TIMELINE : window.Wechat.Scene.SESSION   
}, (data) => {
this.sharePopup = false;

}, reason => {
this.$vux.alert.show({
title: '提示',
content: reason
});

});
}
},
components: {
XHeader,
Swiper,
XButton,
Flexbox,
FlexboxItem,
XImg,
Popup,
XInput,
Card,
Cell,
Badge
},
watch: {}
};



