
import XHeader from 'vux/src/components/x-header/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
data() {
return {
desc: "",
btnClass: "",
imgList: [
]
};
},
created: function() {
let params = this.$route.params;
this.imgList = params.photos;
},
methods: {
next() {
if(this.desc) {
this.$router.push({
path: '/page/release/SelectLabel',
params: {
desc: this.desc
}
});
sessionStorage.setItem('imgDesc',this.desc);
}else {
this.$vux.alert.show({
title: "提示",
content: "图片描述不能为空"
});
}
}
},
components: {
XHeader,
Grid,
GridItem,
XTextarea,
XButton
},
watch: {
desc(desc) {
if(desc) {
this.btnClass = "active";
}else {
this.btnClass = "";
}
}
},
};



