
import XHeader from 'vux/src/components/x-header/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import { openAlbum, takePicture } from "../../cordovaplugin";

export default {
data() {
return {
imgList: {
index: 0,
currentImg: '',
list: []
}
};
},
created: function() {
let params = this.$route.params;
if(params) {
this.imgList.list = params.photos;
this.imgList.currentImg = this.imgList.list[0];
}
},
methods: {
back() {
this.$router.back();
},

selectImg(index) {
this.imgList.index = index;
this.imgList.currentImg = this.imgList.list[index];
},

deleteImg(index) {
this.imgList.list.splice(index,1);
if(this.imgList.list.length<=0) {
this.$router.back();
}
if(index == this.imgList.index) {
this.imgList.currentImg = this.imgList.list[0];
this.imgList.index = 0;
}
},

async takePhoto() {
if(this.imgList.list.length>=9) {
this.$vux.alert.show({
title: "提示",
content: "单次发布最多9张"
});
}else {
let photo = await takePicture();
this.imgList.list.push(photo);
}
},

next() {
this.$router.push({
path: '/page/release/Release',
name: 'Release',
params: {
photos: this.imgList.list
}
});
sessionStorage.setItem('photos', JSON.stringify(this.imgList.list));
}
},
components: {
XHeader,
Flexbox,
FlexboxItem,
XImg
}
};



