
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
data() {
return {
imgInfo: {},
cellArr: [
{
name: "绿亮科创园",
address: "上海市虹梅南路2588号",
righticonclass: "icon-weixuanzhong",
selectClass: "icon-xuanzhong"
},
{
name: "绿亮科创园",
address: "上海市虹梅南路2588号",
righticonclass: "icon-weixuanzhong",
selectClass: ""
},
{
name: "绿亮科创园",
address: "上海市虹梅南路2588号",
righticonclass: "icon-weixuanzhong",
selectClass: ""
},
{
name: "绿亮科创园",
address: "上海市虹梅南路2588号",
righticonclass: "icon-weixuanzhong",
selectClass: ""
}
]
};
},
created: function() {
let params = this.$route.params;
this.imgInfo.desc = params.desc;
},
methods: {
selectAddress(cell) {
this.imgInfo.address = cell;
this.cellArr.forEach((el, index) => {
el.selectClass = "";
});
cell.selectClass = "icon-xuanzhong";
},

next() {
this.$router.push({
path: '/page/release/SelectLabel',
params: {
imgInfo: this.imgInfo
}
});
}
},
components: {
XHeader,
Flexbox,
FlexboxItem,
Card,
Cell,
Group,
XButton
}
};



