

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import routes from './router/index'
import store from './store'
import VueScroller from 'vue-scroller'
import wechatAuth from './assets/js/wechatAuth'

Vue.use(wechatAuth, {appid: 'wxebf7146b68f2e818'});
Vue.use(VueScroller);

import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'


Vue.use(ConfirmPlugin)
Vue.use(ToastPlugin, {position: 'center'})
Vue.use(AlertPlugin)
Vue.use(LoadingPlugin)

Vue.use(VueRouter)
const router = new VueRouter({
routes
})
FastClick.attach(document.body)

Vue.config.productionTip = false
/* eslint-disable no-new */






document.addEventListener('deviceready', function() {
new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')
window.navigator.splashscreen.hide()
}, false);



