
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import util from "../../util";

export default {
data() {
return {
time: 60,
state: true,
mobile: "",
sign: ""
};
},
components: {
XHeader,
XButton,
Group,
XInput
},
created: function() {},
mounted: async function() {
this.mobile = this.$route.query.mobile;
if (this.mobile) {
this.getSign();
}
},
methods: {
async getSign() {
fetch(
`http://www.shedouwang.com/api/ajax/sendssms/?regs-${this.mobile}.html`
)
.then(response => response.json())
.then(result => {
if (result.return_code == "success") {
let data = result.return_data;
this.$vux.toast.show({
text: data,
type: "text"
});
if (data != "1分钟内不能重复获取验证码" && data) {
let timer = setInterval(() => {
if (this.time <= 0) {
clearInterval(timer);
this.state = false;
this.time = 60;
}
--this.time;
}, 1000);
} else {
this.state = true;
}
} else {
this.$vux.alert.show({
title: "提示",
content: result.return_data
});
}
})
.catch(error => {
reject(error);
});
},
async login() {
let data = await util.getData({
url: `/users/login?login_username=${
this.$route.query.mobile
}&login_type=1&mobile_code=${this.sign}`,
method: "get"
});
localStorage.setItem("userInfo", JSON.stringify(data));
this.$vux.toast.show({
text: "登录成功",
type: "text",
onHide: () => {
this.$router.push({
path: "/page/UserIndex"
});
}
});
},
async register() {
let data = await util.getData({
url: `/users/register`,
method: "post",
param: {
mobile: this.$route.query.mobile,
password: this.$route.query.pwd
}
});
this.$vux.toast.show({
text: data,
type: "text",
onHide: () => {
this.$router.push({
path: '/page/user/MobileNumLogin'
});
}
});
},
async resetPwd() {
let data = await util.getData({
url: `/users/setpassword`,
method: "post",
param: {
mobile: this.$route.query.mobile,
password: this.$route.query.pwd,
sign: this.sign
}
});
this.$vux.toast.show({
text: data,
type: "text",
onHide: () => {
this.$router.push({
path: "/page/user/LoginIndex"
});
}
});
}
}
};



