
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import util from "../../util";

export default {
data() {
return {

pageShow: false,

disnId: "",

designItem: null,

commentList: null,
pageNum: 1,

avatarList: null,

commentCount: 0,

designImgList: [],

userInfo: null,

messageBadge: "80",

comment: "",

cellArr: [





{
title: "支付宝",
lefticonclass: "icon-zhifubao",
righticonclass: "icon-weixuanzhong"
}
],

popup: {
popupState: false,
type: "reward"
},
rewards: {
list: [0.01, 0.1, 0.5, 1, 5, 10],
seleted: -1
},
selectPayMethod: "",

confirmReward: "",

menuBoxShow: false,

showReportPopup: false,

reportCellArr: [
{
title: "淫秽色情",
lefticonclass: "",
righticonclass: "icon-weixuanzhong"
},
{
title: "违法信息",
lefticonclass: "",
righticonclass: "icon-weixuanzhong"
},
{
title: "营销广告",
lefticonclass: "",
righticonclass: "icon-weixuanzhong"
},
{
title: "恶意攻击谩骂",
lefticonclass: "",
righticonclass: "icon-weixuanzhong"
}
]
};
},
created: async function() {
this.$vux.loading.show({
text: "Loading"
});
/**
* 获取设计师详情
*/
let disnId = this.$route.query.id;
this.disnId = disnId;
let userInfo = localStorage.getItem("userInfo");
let detailsUrl;
let commentListUrl;
if (userInfo) {
this.userInfo = JSON.parse(userInfo);
detailsUrl = `/materials/details?sources_id=${disnId}&loginid=${
this.userInfo.id
}`;
commentListUrl = `/materials/commentslists?loginid=${
this.userInfo.id
}&sources_id=${disnId}&pages=${this.pageNum}&rowsmaxs=10`;
} else {
detailsUrl = `/materials/details?sources_id=${disnId}`;
commentListUrl = `/materials/commentslists?sources_id=${disnId}&pages=${this.pageNum}&rowsmaxs=10`;
}
this.designItem = await util.getData({
url: detailsUrl,
method: "get"
});
this.designItem.images.forEach((item, index) => {
this.designImgList.push({
img: item.thumbs
});
});

this.commentList = await util.getData({
url: commentListUrl,
method: "get"
});
this.pageNum++;

this.avatarList  = await util.getData({
url: `/materials/praisepayers?loginid=${this.designItem.userid}`,
method: "get"
});

this.commentCount = await util.getData({
url: `/materials/commenttotals?sources_id=${disnId}`,
method: "get"
});

this.designItem.sex = this.designItem.gender == 1 ? "boy" : "girl";
this.$vux.loading.hide();
this.pageShow = true;
},
mounted: function() {},
methods: {

showMenu(designItem) {
this.menuBoxShow = !this.menuBoxShow;
this.currentDesignItem = designItem;
},

toogleReportPopup() {
this.menuBoxShow = false;
this.showReportPopup = !this.showReportPopup;
},

selectReport(report) {
this.confirmContent = report.title;
this.reportCellArr.forEach((item, index) => {
item.righticonclass = "icon-weixuanzhong";
});
report.righticonclass = "icon-xuanzhong";
},

async reportCommit() {
if (this.confirmContent) {
this.$vux.loading.show({
text: "Loading"
});
let data = await util.request({
url: `/materials/reports`,
method: "post",
param: {
loginid: this.userInfo.id,
users_mains_id: this.currentDesignItem.userid,
reasons: this.confirmContent,
types: 2,
targets_id: this.disnId
}
});
if (data.return_code == "success") {
this.$vux.toast.show({
text: data.return_data
});
this.showReportPopup = false;
this.confirmContent = "";
} else {
this.$vux.alert.show({
title: "提示",
content: data.return_data
});
}
this.$vux.loading.hide();
} else {
this.$vux.alert.show({
title: "提示",
content: "请选择举报内容"
});
}
},

show(index, list) {
this.previewList.index = index;
this.previewList.list = list;
},

async follow(users) {
let types = users.followed ? 0 : 1;
if (this.userInfo) {
let data = await util.request({
url: `/materials/usersfollows`,
method: "post",
param: {
users_mains_id: users.userid,
loginid: this.userInfo.id,
types: types
}
});
if (data.return_code == "success") {
users.followed = types;
}
this.$vux.toast.show({
text: data.return_data
});
} else {
this.$vux.confirm.show({
title: "提示",
content: "请先登录",
onConfirm: () => {
this.$router.push({
path: "/page/user/LoginIndex"
});
}
});
}
},

async liking(users) {
if (this.userInfo) {
if (users.score == 1) {
this.$vux.alert.show({
title: "提示",
content: "已经给过差评了"
});
} else if (users.score == 5) {
this.$vux.alert.show({
title: "提示",
content: "不能重复点赞"
});
} else {
let data = await util.request({
url: `/materials/scores`,
method: "post",
param: {
users_mains_id: users.userid,
loginid: this.userInfo.id,
types: 1,
scores: 5,
targets_id: this.disnId
}
});
if (data.return_code == "success") {
this.$vux.toast.show({
text: data.return_data
});
users.score = 5;
users.praisesnums++;
} else {
this.$vux.alert.show({
title: "提示",
content: data.return_data
});
}
}
} else {
this.$vux.confirm.show({
title: "提示",
content: "请先登录",
onConfirm: () => {
this.$router.push({
path: "/page/user/LoginIndex"
});
}
});
}
},

async bading(users) {
if (this.userInfo) {
if (users.score == 5) {
this.$vux.alert.show({
title: "提示",
content: "已经给过好评了"
});
} else if (users.score == 1) {
this.$vux.alert.show({
title: "提示",
content: "不能重复差评"
});
} else {
let data = await util.request({
url: `/materials/scores`,
method: "post",
param: {
users_mains_id: users.userid,
loginid: this.userInfo.id,
types: 1,
scores: 1,
targets_id: this.disnId
}
});
if (data.return_code == "success") {
this.$vux.toast.show({
text: data.return_data
});
users.score = 1;
users.badsnums++;
} else {
this.$vux.alert.show({
title: "提示",
content: data.return_data
});
}
}
} else {
this.$vux.confirm.show({
title: "提示",
content: "请先登录",
onConfirm: () => {
this.$router.push({
path: "/page/user/LoginIndex"
});
}
});
}
},

async commentsCommit() {
if (!this.userInfo) {
this.$vux.confirm.show({
title: "提示",
content: "请先登录",
onConfirm: () => {
this.$router.push({
path: "/page/user/LoginIndex"
});
}
});
return false;
}
if (!this.comment) {
this.$vux.alert.show({
title: "提示",
content: "评论内容不能为空"
});
return false;
}
let data = await util.request({
url: `/materials/comments`,
method: "post",
param: {
users_mains_id: this.designItem.userid,
loginid: this.userInfo.id,
sources_id: this.disnId,
comments: this.comment
}
});
if (data.return_code == "success") {
this.comment = '';
this.commentCount++;
this.$vux.toast.show({
text: data.return_data
});

this.commentList = await util.getData({
url: `/materials/commentslists?sources_id=${this.disnId}&loginid=${
this.userInfo.id
}`,
method: "get"
});
} else {
this.$vux.alert.show({
title: "提示",
content: data.return_data
});
}
},

async commentLiking(comment) {
if (this.userInfo) {
if (comment.praised == 1) {
this.$vux.alert.show({
title: "提示",
content: "不能重复点赞"
});
} else {
let data = await util.request({
url: `/materials/scores`,
method: "post",
param: {
users_mains_id: comment.userid,
loginid: this.userInfo.id,
types: 2,
scores: 5,
targets_id: comment.id
}
});
if (data.return_code == "success") {
this.$vux.toast.show({
text: data.return_data
});
comment.praised = 1;
comment.praisesnums++;
} else {
this.$vux.alert.show({
title: "提示",
content: data.return_data
});
}
}
} else {
this.$vux.confirm.show({
title: "提示",
content: "请先登录",
onConfirm: () => {
this.$router.push({
path: "/page/user/LoginIndex"
});
}
});
}
},

togglePopup(type) {
this.popup.type = type;
this.popup.popupState = !this.popup.popupState;
},
rewardSelected(index) {
this.confirmReward = this.rewards.list[index];
this.rewards.seleted = index;
this.toPay();
},
toPay() {
this.popup.type = "pay";
},
backSelectReward() {
this.confirmReward = "";
this.popup.type = "reward";
},
selectPay(cell) {
this.selectPayMethod = cell.title;
this.cellArr.forEach((item, index) => {
item.righticonclass = "icon-weixuanzhong";
});
cell.righticonclass = "icon-xuanzhong";
},

async payCommit() {
if (this.userInfo) {
if (!this.selectPayMethod) {
this.$vux.alert.show({
title: "提示",
content: "请选择付款方式"
});
} else if (this.selectPayMethod == "支付宝") {
let data = await util.getData({
url: `/materials/praisepays`,
method: "post",
param: {
loginid: this.userInfo.id,
users_mains_id: this.designItem.userid,
moneys: this.confirmReward
}
});
const orderstring = data; 
window.Alipay.pay(
orderstring,
res => {
this.$vux.toast.show({
text: "打赏成功"
});
},
e => {
console.error(e);
}
);
}
} else {
this.$vux.confirm.show({
title: "提示",
content: "请先登录",
onConfirm: () => {
this.$router.push({
path: "/page/user/LoginIndex"
});
}
});
}
},
onInfinite(done) {
if(this.pageNum == 1) {
done();
return false;
}
setTimeout(async () => {
let detailsUrl,commentListUrl;
if (this.userInfo) {
detailsUrl = `/materials/details?sources_id=${this.disnId}&loginid=${
this.userInfo.id
}`;
commentListUrl = `/materials/commentslists?loginid=${
this.userInfo.id
}&sources_id=${this.disnId}&pages=${this.pageNum}&rowsmaxs=10`;
} else {
detailsUrl = `/materials/details?sources_id=${this.disnId}`;
commentListUrl = `/materials/commentslists?sources_id=${this.disnId}&pages=${this.pageNum}&rowsmaxs=10`;
}
let commentList = await util.getData({
url: commentListUrl,
method: "get"
});
if(commentList == '') {
this.$refs.scroller.finishInfinite(2);
return false;
}
this.commentList = this.commentList.concat(commentList);
this.pageNum++,
done();
}, 1000);
},
toDetail(userId) {
this.$router.push({
path: '/page/user/MyRelease',
name: 'MyRelease',
params: {
id: userId,
type: 'other'
}
});
},
},
components: {
XHeader,
Swiper,
XButton,
Flexbox,
FlexboxItem,
XImg,
Badge,
Popup,
Card,
Cell,
XInput
}
};



