
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import util from "../../util";

export default {
data() {
return {
userInfo: null,
myFanList: [],
pageNum: 1
};
},
components: {
XHeader,
XButton,
Flexbox,
FlexboxItem
},
created: async function() {
let userInfo = localStorage.getItem("userInfo");
if (userInfo) {
this.userInfo = JSON.parse(userInfo);
this.$vux.loading.show({
text: "loading..."
});
let data = await util.getData({
url: `/users/myfans?loginid=${this.userInfo.id}&pages=${
this.pageNum
}&rowsmax=10`,
method: "get"
});
this.myFanList = data;
this.myFanList.forEach(items => {
items.forEach(item => {
item.gender == 1 ? (item.sex = "boy") : (item.sex = "girl");
});
});
this.pageNum++;
this.$vux.loading.hide();
}
},
methods: {
toDetail(userId) {
this.$router.push({
path: '/page/user/MyRelease',
name: 'MyRelease',
params: {
id: userId,
type: 'other'
}
});
},

async follow(users, index) {
let types = users.followed == 1 ? 0 : 1;
if (this.userInfo) {
let data = await util.request({
url: `/materials/usersfollows`,
method: "post",
param: {
users_mains_id: users.id,
loginid: this.userInfo.id,
types: types
}
});
if (data.return_code == "success") {
users.followed = types;
}
this.$vux.toast.show({
text: data.return_data
});
} else {
this.$vux.confirm.show({
title: "提示",
content: "请先登录",
onConfirm: () => {
this.$router.push({
path: "/page/user/LoginIndex"
});
}
});
}
},
onInfinite(done) {
if(this.pageNum == 1) {
done();
return false;
}
setTimeout(async () => {
let data = await util.getData({
url: `/users/myfans?loginid=${this.userInfo.id}&pages=${
this.pageNum
}&rowsmax=10`,
method: "get"
});
if (data == "") {
this.$refs.scroller.finishInfinite(2);
return false;
}
this.myFanList = this.myFanList.concat(data);
this.myFanList.forEach(items => {
items.forEach(item => {
item.gender == 1 ? (item.sex = "boy") : (item.sex = "girl");
});
});
this.pageNum++;
done();
}, 1000);
}
}
};



