
import XHeader from 'vux/src/components/x-header/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import util from "../../util";

export default {
directives: {
TransferDom
},
data() {
return {
title: '我的收藏',
imgList: []
};
},
components: {
XHeader,
XImg,
Grid,
GridItem,
Previewer
},
created: async function() {
let userInfo = JSON.parse(localStorage.getItem("userInfo"));
let params = this.$route.params;

if (userInfo) {
this.$vux.loading.show({
text: "loading..."
});
if (params.id) {
this.title = '发布信息';
let data = await util.getData({
url: `/materials/details?loginid=${userInfo.id}&sources_id=${
params.id
}`,
method: "get"
});
this.imgList = data.images;
this.imgList.forEach(item => {
item.src = item.msrc = item.updatas;
});
} else {
let data = await util.getData({
url: `/users/mycollects?loginid=${userInfo.id}`,
method: "get"
});
this.imgList = data;
this.imgList.forEach(item => {
item.src = item.msrc = item.thumbs;
});
}
this.$vux.loading.hide();
}
},
methods: {
show(index) {
this.$refs.previewer.show(index);
}
}
};



