
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import util from "../../util";

export default {
data() {
return {
pwdShowState: false,
password: "password",
mobile: "",
time: 60,
state: false,
pwd: "",
mobile_code: "",
timer: null
};
},
components: {
XHeader,
XButton,
Group,
XInput
},
created: function() {
if(this.timer) {
clearInterval(this.timer);
}
},
methods: {
async getSign() {
fetch(
`http://www.shedouwang.com/api/ajax/sendssms/?regs-${this.mobile}.html`
)
.then(response => response.json())
.then(result => {
if (result.return_code == "success") {
let data = result.return_data;
this.$vux.toast.show({
text: data,
type: "text"
});
if (data != "1分钟内不能重复获取验证码" && data) {
this.state = true;
this.timer = setInterval(() => {
if (this.time <= 0) {
clearInterval(this.timer);
this.state = false;
this.time = 60;
}
--this.time;
}, 1000);
} else {
this.state = false;
}
} else {
this.$vux.alert.show({
title: "提示",
content: result.return_data
});
}
})
.catch(error => {
this.$vux.alert.show({
title: "提示",
content: error
});
});
},
pwdToggle() {
this.pwdShowState = !this.pwdShowState;
},

async commit() {
let wechatUserinfo = this.$route.params.wechatUserinfo;
let mobile = this.mobile;
let pwd = this.pwd;
let mobile_code = this.mobile_code;
if (
mobile.match(
/^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/
) &&
pwd
) {
let data = await util.getData({
url: `/oauth/bands`,
method: "post",
param: {
openid: wechatUserinfo.openid,
nickname: wechatUserinfo.nickname,
sex: wechatUserinfo.sex,
language: wechatUserinfo.language,
headimgurl: wechatUserinfo.headimgurl,
unionid: wechatUserinfo.unionid,
mobile: mobile,
password: pwd,
mobile_code: mobile_code
}
});
localStorage.setItem('userInfo',JSON.stringify(data));
this.$vux.toast.show({
text: '提交成功',
onHide: () => {
this.$router.push({
path: '/'
});
}
});
} else {
this.$vux.alert.show({
title: "提示",
content: "请输入正确的手机号或密码"
});
}
}
},
watch: {
pwdShowState(pwdShowState) {
if (pwdShowState) {
this.password = "text";
} else {
this.password = "password";
}
}
}
};



