
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Popup from 'vux/src/components/popup/index.vue'

import { openAlbum, takePicture } from "./cordovaplugin";
import wechatAuth from './assets/js/wechatAuth'

const qs= require('qs');

export default {
name: "app",
data() {
return {
photos: [],
transitionName: "vux-pop",
showTabbar: true,
releaseShowState: false,
userInfo: null
};
},
created: function() {
switch (this.$route.path) {
case "/":
this.showTabbar = true;
break;
case "/page/UserIndex":
this.showTabbar = true;
break;

default:
this.showTabbar = false;
break;
}
},
methods: {
releaseShow() {
if(localStorage.getItem('userInfo')) {
this.releaseShowState = true;
}else {
this.$vux.confirm.show({
title: "提示",
content: "请先登录",
onConfirm: () => {
this.$router.push({
path: "/page/user/LoginIndex"
});
}
});
}
},
async takephoto() {
let photo = await takePicture();
this.photos.push(photo);
this.$router.push({
path: "/page/release/ReleaseImgEdit",
name: "ReleaseImgEdit",
params: {
photos: this.photos
}
});
this.releaseShowState = false;
},
async selectImg() {
let photos = await openAlbum();
if(photos.length) {
this.$router.push({
path: "/page/release/ReleaseImgEdit",
name: "ReleaseImgEdit",
params: {
photos: this.photos.concat(photos)
}
});
}
this.releaseShowState = false;
}
},
watch: {
$route(to, from) {
switch (to.path) {
case "/":
this.showTabbar = true;
break;
case "/page/UserIndex":
this.showTabbar = true;
break;

default:
this.showTabbar = false;
break;
}





}
},
components: {
Tabbar,
TabbarItem,
Popup
}
};



